// Coloring
$color-primary__red: #CB0000;
$color-primary__red-dark: #800000;
$color-text__default: #231F20;
$color-text__gray: #808184;
$color-bg-white: #F0F1F1;
$color-white--light: rgba(white, .25);
$color-bg-dark: #231F20;
$color-bg-gray: #A6A8AB;
$color-border: #808184;
$color-border--light: lighten( $color-border, 40% );

// Typography
$roboto: "Roboto", sans-serif;
$roboto-thin: "Roboto-Thin", sans-serif;
$roboto-light: "Roboto-Light", sans-serif;
$roboto-light__italic: "Roboto-LightItalic", sans-serif;
$roboto-medium: "Roboto-Medium", sans-serif;
$roboto-bold: "Roboto-Bold", sans-serif;

$font_body: $roboto-light;