%transition-default{
	transition: all 350ms ease;
}
%reset_list{
	list-style: none outside none;
	margin: 0;
	padding: 0;
}
%reset-border{
	border: none;
}
%reset-border-radius{
	border-radius: 0;
}
// layout
%d_flex{
	display: flex;
}
%justify_content__center{
	justify-content: center;
}
%justify_content__start{
	justify-content: flex-start;
}
%justify_content__end{
	justify-content: flex-end;
}
%justify_content__between{
	justify-content: space-between;
}
%justify_content__around{
	justify-content: space-around;
}
%align_items__center{
	align-items: center;
}
%align_items__start{
	align-items: flex-start;
}
%align_items__end{
	align-items: flex-end;
}
%align_items__stretch{
	align-items: stretch;
}

// -- SIZING
%size_16{
	height: 16px;
	width: 16px;
}
%size_18{
	height: 18px;
	width: 18px;
}
%size_21{
	height: 21px;
	width: 21px;
}
%size_24{
	height: 24px;
	width: 24px;
}
%size_28{
	height: 28px;
	width: 28px;
}
%size_30{
	width: 30px;
	height: 30px;
}
%size_32{
	height: 32px;
	width: 32px;
}
%size_42{
	height: 42px;
	width: 42px;
}

%clearfix{
	&:before,
	&:after{
		clear: both;
		display: table;
		content: '';
	}
}

%shadow_default{
	box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}

%linear-gradient{
	background: linear-gradient(to right, #7a0909, #b80000);
	background-color: #7a0909;
}