.custom-nav{

	@include max1024{
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		align-items: center;
		margin-top: 25px;
	}
	&__prev,
	&__next{
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 3;
		@extend %transition-default;

		@include max1024{
			position: relative;
			top: 0;
			transform: translateY(0);
			margin: 0 8px;
		}
		&:hover,
		&:focus{
			opacity: .65;
		}
	}
	&__prev{
		left: -30px;

		@include max1024{
			left: initial;
		}
	}
	&__next{
		right: -30px;

		@include max1024{
			right: initial;
		}
	}

	&__icon{
		width: 18px;
		height: 18px;

		@include max1024{
			width: 12px;
			height: 12px;
		}
	}
}

