.blockquote{
	&__with{
		&-image{
			display: flex;
			flex-flow: row wrap;
			justify-content: flex-start;
			align-items: stretch;
			margin: 60px 0;

			@include max1024{
				margin: 45px 0;
			}

			.blockquote{
				&__content{
					flex: 0 0 40%;
					max-width: 40%;
					padding: 15px 30px;
					padding-right: 60px;
					border-left: 4px solid $color-primary__red;
					display: flex;
					flex-flow: row wrap;
					justify-content: center;
					align-items: center;

					@include max1024{
						order: 2;
						flex: 0 0 100%;
						max-width: 100%;
						padding: 20px 30px;
					}

					p{
						font-family: $roboto-light;
						font-size: 25px;
						line-height: 1.5;

						@include max1024{
							font-size: 28px;
						}
						@include max768{
							font-size: 21px;
						}
					}
				}
				&__image{
					flex: 0 0 60%;
					max-width: 60%;
					min-height: 320px;

					background-repeat: no-repeat;
					background-size: cover;
					background-position: center center;

					@include max1024{
						flex: 0 0 100%;
						max-width: 100%;
						min-height: 280px;
					}
				}
			}
		}
	}
}