.bg{
	&-red{
		background-color: $color-primary__red;
		color: white;

		&__linear{
			color: white;
			background-image: linear-gradient( to right, $color-primary__red-dark 10%, $color-primary__red 90% );
		}
	}
	&-image{
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
	}
	&-white{
		&__light{
			background: #fdfdfd;
		}
		&__rgb{
			background: rgba( white, .75 );
		}
	}
	&-gray{
		background: #d8d8d8;
	}
	&-gradient{
		background: linear-gradient(to right, #7a0909, #b80000);	
	}
}