.service-header {
  padding: 80px 0;
  text-align: center;

  &__title {
    text-align: center;
    margin: 0 auto;
    color: #cb0000;
    margin-bottom: 35px;

    &--first {
      display: block;
      font-size: 48px;
      font-family: "Roboto-Bold";
      line-height: 1;
      margin-bottom: 10px;
    }

    &--second {
      display: block;
      font-size: 38px;
      font-family: "Roboto-LightItalic";
      line-height: 1;
    }
  }

  &__paragraph {
    font-size: 22px;
  }
}

.service-link {
    font-family: "Roboto";
    font-size: 22px;
    text-align: right;
    color: #cb0000;
    padding-bottom: 5px;
    border-bottom: 1px solid currentColor;

    &__container{
        text-align: right;
    }

    &:hover{
        text-decoration: none;
    }
  }

.service-operational-research {
  .col-5 {
    background-color: #7a0909;
    // background: linear-gradient(to right, #7a0909, #b80000);
    padding: 0px 6%;
    padding-top: 100px;
    padding-bottom: 130px;
  }

  .section-title {
    color: #fff;
    margin-bottom: 35px;
  }

  &__paragraph {
    color: #fff;
    font-size: 20px;
    margin-bottom: 80px;
    line-height: 1.5;
  }

  .col-7 {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    padding: 0;
  }

  .service-link {
    color: #fff;
  }
}

.service-user-research {
  padding: 100px 0;

  .col-7 {
    padding: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
  }

  .col-5 {
    padding: 0;
    padding-top: 20px;
    padding-bottom: 120px;
    padding-left: 5%;
    padding-right: 8%;
  }

  .section-title {
    color: #cb0000;
    margin-bottom: 35px;
  }

  &__paragraph {
    font-size: 20px;
    margin-bottom: 30px;
    line-height: 1.5;
  }

}
