@import 'background';
@import 'space';

.clear-offset{
	&--1024{
		@include max1024{
			margin-left: 0;
			margin-right: 0;
		}
	}
}
.fullwidth{
	&-1024{
		@include max1024{
			flex: 0 0 100%;
			max-width: 100%;
		}
	}
}

.mb{
	&-10{
		margin-bottom: 10px;
	}
}

.max{
	&-90{
		max-width: 90%;
	}
}
// text size
.text{
	&-20{
		font-size: 20px !important;
	}
	&-18{
		font-size: 18px !important;
		@include max1024{
			font-size: 14px !important;
		}
	}
	&-16{
		font-size: 16px !important;
		&__768{
			@include max768{
				font-size: 16px !important;
			}
		}
	}
	&-14{
		font-size: 14px !important;
		&__all{
			font-size: 14px !important;
			@include max576{
				font-size: 12px !important;
			}
		}
		&__768{
			@include max768{
				font-size: 14px !important;
			}
		}
	}
}

@include max1024{
	.text-16__1024{
		font-size: 16px !important; 
	}
}

@import 'text-color';
@import 'text-weight';
@import 'visibility';


.fill{
	&-red{
		fill: $color-primary__red;
		stroke: red;
	}
}

.uppercase{
	text-transform: uppercase;
}

.clear{
	&-link{
		border-bottom: none !important;
		text-decoration: none !important;
		padding-bottom: none !important;
	}
	&-br{
		&--1024{
			@include max1024{
				br{
					display: none;
				}
			}
		}
	}
}

.d{
	&-ib{
		display: inline-block;
	}
	&-b{
		display: block;
	}
	&-flex{
		display: flex;
	}
}
.align-items{
	&__center{
		align-items: center;
	}
	&__start{
		align-items: start;
	}
	&__end{
		align-items: end;
	}
	&__stretch{
		align-items: stretch;
	}
}

.no-border{
	border: none;
}

.size{
	&-16{
		@extend %size_16;
	}
}