.brand-slider{
	&__wrapper{
		position: relative;
		@include max768{
			overflow: hidden;
		}
	}
}
.brand-sliders{
	overflow: hidden;
	button{
		display: none !important;
	}
	.slick{
		&-list{
			margin-left: -20px;
			margin-right: -20px;
		}

		&-slide{
			margin: 0 20px;
		}
		&-slider{
			overflow: hidden;
		}
	}
	.brand-slide{
		img{
			display: inline-block;
			margin-left: auto;
			margin-right: auto;
		}
		&__wrapper{
			height: 102px;
			width: 248px;
			margin: auto;
			display: flex;
			img{
				object-fit: contain;
			}
		}
	}
}