form{
	[type="text"],
	[type="email"],
	[type="tel"],
	[type="password"],
	select,
	textarea{
		border: 1px solid $color-border;
		border-radius: 6px;
		padding: 12px 16px;
		// font-size: 15px;
		width: 100%;
		font-family: $roboto;

		@include max1024{
			font-size: 14px;
			border-radius: 3px;
			padding: 8px 16px;
		}
	}
	.form-field{
		+ .form-field{
			margin-top: 15px;
		}
	}
}

.form-field{
	display: block;
	// checkbox
	&__checkbox{
		input[type="checkbox"]{
	        position: absolute;
	        font-size: 0;
	        color: transparent;
			opacity: 0;
	        text-indent: -9999px;
		}
		label{
			margin: 0;
			cursor: pointer;
			position: relative;
			transition: all 250ms ease;
			padding-left: 25px;
			font-size: 14px;
			@include max576{
				font-size: 12px;
			}
			&:before,
			&:after{
			    content: '';
			    position: absolute;
			    transition: all 250ms ease;
			}
			&:before{
			    width: 16px;
			    height: 16px;
			    border: 1px solid $color-border--light;
			    border-radius: .2em;
			    left: 0;
			}
			&:after{
			    left: 4.5px;
			    top: 4px;
			
			    height: 4px;
			    width: 8px;
			    display: block;
			    background: transparent;
			    border-left: 1px solid $color-primary__red;
			    border-bottom: 1px solid $color-primary__red;
			    transform: rotate(-40deg);
			
			    opacity: 0;
			    visibility: hidden;
			}
		}
	}

	// radio
	&__radio{
		input[type="radio"]{
	        position: absolute;
	        font-size: 0;
	        color: transparent;
			opacity: 0;
	        text-indent: -9999px;

	        + label{
	        	&:before{
	        		bottom: 3px;
	        	}
	        	&:after{
	        		top: 9px;
	        		@include max576{
	        			top: 5px;
	        		}
	        	}
	        }

	        &:checked{
	        	+ label{
	        		&:after{
	        			opacity: 1;
	        			visibility: visible;
	        		}
	        	}
	        }
		}
		label{
			margin: 0;
			cursor: pointer;
			position: relative;
			transition: all 250ms ease;
			padding-left: 25px;
			font-size: 14px;
			@include max576{
				font-size: 12px;
			}
			&:before,
			&:after{
			    content: '';
			    position: absolute;
			    transition: all 250ms ease;
			}
			&:before{
			    width: 16px;
			    height: 16px;
			    border: 1px solid $color-border--light;
			    border-radius: .2em;
			    left: 0;
			}
			&:after{
			    left: 4.5px;
			    top: 4px;
			
			    height: 4px;
			    width: 8px;
			    display: block;
			    background: transparent;
			    border-left: 1px solid $color-primary__red;
			    border-bottom: 1px solid $color-primary__red;
			    transform: rotate(-40deg);
			
			    opacity: 0;
			    visibility: hidden;
			}
		}
	}
}

.form{
	&-subscribe{
		@extend %d_flex;
		@extend %justify_content__start;
		@extend %align_items__start;
		flex-flow: row wrap;

		@include max576{
			justify-content: flex-start !important;
		}

		input{
			@extend %reset-border;
			@extend %reset-border-radius;
			@extend %shadow_default;
			height: 50px;
			line-height: 50px;
			padding-top: 0;
			padding-bottom: 0;
			width: 100%;

			@include max576{
				height: 38px;
				line-height: 38px;
			}

			&:focus{
				outline: none;
			}
			&[type="submit"]{
				text-align: center;
			}
		}

		.form{
			&-field{
				flex: 1;
				@include max576{
					flex: 0 0 100%;
					max-width: 100%;
				}
			}
			&-submit{
				flex: 0 0 145px;
				max-width: 145px;
				@include max576{
					flex: 0 0 125px;
					max-width: 125px;
					margin-top: 10px;
				}
			}
		}
		p + p{
			margin-top: 0;
		}
	}
	&-contact{
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-start;
		align-items: stretch;
		margin-left: -15px;
		margin-right: -15px;

		.form-field{
			padding-left: 15px;
			padding-right: 15px;
			flex: 0 0 (100%/3);
			max-width: (100%/3);
			margin-bottom: 30px;

			display: flex;
			flex-flow: row wrap;
			justify-content: flex-start;
			align-items: flex-start;

			@include max1024{
				flex: 0 0 50%;
				max-width: 50%;
			}
			@include max768{
				flex: 0 0 100%;
				max-width: 100%;
				margin-bottom: 15px;
			}

			+ .form-field{
				margin-top: 0;				
			}

			&__submit,
			&__message{
				flex: 0 0 100%;
				max-width: 100%;
			}

			&__submit{
				text-align: center;
				justify-content: center;
				align-items: center;

				margin-top: 15px !important;
				margin-bottom: 0;
			}

			input{
				+ input{
					margin-top: 15px;
				}
			}

			[type="submit"]{
				display: inline-block;
				padding: 8px 42px;
				font-size: 25px;
				color: $color-primary__red;
				border: 1px solid $color-primary__red;
				background: transparent;
				text-align: center;
				font-family: $roboto;
				@extend %transition-default;

				@include max1024{
					font-size: 18px;
				}

				&:hover,
				&:focus{
					color: white;
					background: $color-primary__red;
				}
			}
		}
	}
	&-download-resources{
		input[type="text"],
		input[type="email"]{
			border-radius: 0;
			border: none;
			padding: 6px;
			font-size: 13px;
			width: 90%;
		}
		.form-field{
			&--info{
				font-size: 12px;
			}
		}
	}
	&-contact{
		&__page{
			@include placeholder{
				color: $color-text__gray;
			}
			.form-field{
				margin-bottom: 30px;
				+ .form-field{
					margin-top: 0 !important;
				}

				input[type="text"],
				input[type="email"],
				input[type="tel"],
				textarea{
					border-radius: 0;
					border: none;
					background: $color-bg-white;

					&:focus,
					&:active{
						outline: none;
					}
				}

				.btn{
					min-width: 130px;
				}
			}
		}
	}
	&-field{
		&--info{
			font-size: 14px;
		}
		&--submit{
			text-align: center;
		}
	}
}