// sticky
.sticky-wrapper{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 33;
  &.nav-sticky{
    .nav-container{
      background: linear-gradient(to right, #7a0909, #b80000);
      box-shadow: 0px 7px 30px rgba(0,0,0, .2);
    }
    .navigation{
      // padding-top: 10px;
      // padding-bottom: 0px;
      &__list{
        li{
          > a{
            padding-top: 20px;
            padding-bottom: 20px;
          }
        }
      }
    }
    .navigation__header-responsive-collapse{
      top: 51px;
    }
  }

  .navigation__header-responsive-collapse{
    top: 52px;
  }

  + .single-page{
    padding-top: 175px;
    @include max1024{
      padding-top: 130px; 
    }
    &.nospace-top{
      padding-top: 0;
    }
    
    &.single-page__contact{
      padding-top: 120px;
    }
  }
}
.nav-container{
  width: 100%;
  left: 0;
  top: 0;
  transition: all 350ms ease;
  &__inner{
    background: linear-gradient(to right, #7a0909, #b80000);
    position: relative;
  }
}

.navigation{
  width: 100%;
  max-width: 1660px;
  padding: 0px 30px;
  margin-left: auto;
  margin-right: auto;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  transition: all 350ms ease;

  @include max1024{
    padding-top: 15px;
    padding-bottom: 15px;
  }

  @include max768{
    padding-left: 15px;
    padding-right: 15px;
  }
  &__logo{
    width: 165px;
    flex: 0 0 165px;
    max-width: 165px;
    @include max1024{
      width: 145px;
      flex: 0 0 145px;
      max-width: 145px;
    }
    a{
      display: block;
    }
  }

  &__header{
    flex: 1;

    @include max1024{
      display: none;
    }

    &-rwd{
      display: none;
      flex: 1;
      position: relative;

      @include max1024{
        display: block;
      }
    }

    &-hamburger{
      cursor: pointer;
      position: relative;
      width: 26px;
      height: 26px;
      display: block;
      margin-left: auto;

      .hamburger-content{
        position: absolute;
        top: 4px;
        left: 0;

        width: 26px;
        height: 2px;
        background-color: white;
        display: block;

        transition: transform .15s ease;
        &:before,
        &:after{
            content: '';
            display: block;
            position: absolute;
            background-color: white;
            width: 26px;
            height: 2px;
            transition-timing-function: ease;
            transition-duration: .15s;
            transition-property: transform, opacity;
        }
        &:before{
            top: 8px;
        }
        &:after{
            top: 16px;
        }
      }

      &[aria-expanded="true"]{
        .hamburger-content{
          transform: translate3d(0, 8px, 0) rotate(45deg);
          &:before{
            transform: rotate(-45deg) translate3d(5px,-8px,0);
            opacity: 0;
          }
          &:after{
            transform: translate3d(0,-16px,0) rotate(-90deg);
          }
        }
      }
    }

    &-responsive{
      position: relative;
      &-collapse{
        position: absolute;
        top: 100%;
        // top: 51px;
        right: 0;

        .navigation{
          &__list{
            min-width: 175px;
            background: $color-primary__red;
            // background: linear-gradient(to right, #7a0909, #b80000);
            display: block;

            .submenu-toggle{
              position: relative;
              // padding-right: 20px;
              // &:after{
              //   content: '';
              //   position: absolute;
              //   right: 15px;
              //   top: 11px;
              //   width: 7px;
              //   height: 7px;
              //   border-bottom: 1px solid white;
              //   border-right: 1px solid white;
              //   transform: rotate(45deg);
              // }
            }

            .sub-menu{
              position: relative;
              transform: translateY(0) translateX(0) !important;
              left: 0 !important;
              opacity: 1;
              visibility: visible;
              li{
                > a{
                  padding-left: 30px !important;
                  text-align: left;
                }
              }

              &.hidden{
                display: none;
              }

            }
          }
          &__item{
            margin: 0 !important;
            display: block;
            a{
              font-size: 12px;
              padding: 8px 13px !important;
              border: none;
              display: block
            }
          }
        }
      }
    }
  }

  &__list{
    list-style: none outside none;
    margin: 0;
    padding: 0;

    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    align-items: center;

    li{
      display: inline-block;
      margin: 0px 30px;
      position: relative;

      &:first-child{
        margin-left: 0;
      }
      &:last-child{
        margin-right: 0;
      }

      &:hover,
      &:focus,
      &.active{
        > a{
          opacity: .65;
          border-bottom-color: white;
        }
        > .sub-menu{
          opacity: 1;
          visibility: visible;
          transform: translateY(0) translatex(-50%);
        }
      }
      > a{
        display: block;
        padding: 30px 0;
        // border-bottom: 2px solid transparent;
        font-size: 13px;
        font-family: $roboto-medium;
        text-transform: uppercase;
        color: white;
        transition: all 350ms ease;
      }
      
      > ul{
        position: absolute;
        top: 100%;
        left: 50%;
        min-width: 180px;
        background: $color-primary__red;
        // background: linear-gradient(to right, #7a0909, #b80000);
        padding: 0;
        text-align: center;
        transform: translateY(-15px) translateX(-50%);
        transition: all 350ms ease;
        opacity: 0;
        visibility: hidden;
        li{
          margin: 0;
          display: block;
          > a{
            display: block;
            padding: 10px 18px !important;
            border-bottom: none;
            &:hover,
            &:focus{
              opacity: .65;
            }
          }
        }
      }

    }
  }
}

