.item-inquiries{
	padding: 30px;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
	align-items: flex-start;

	@include max1024{
		padding: 20px;
	}

	+ .item-inquiries{
		@include max1024{
			padding-top: 0;
		}
	}

	&:last-child{
		padding-bottom: 45px;
	}

	&__icon{
		flex: 0 0 30px;
		max-width: 30px;

		display: flex;
		flex-flow: row wrap;
		justify-content: flex-start;
		align-items: flex-start;
		padding-top: 5px;
	}
	&__box{
		flex: 1;
	}

	&__title{
		font-family: $roboto-bold;
		font-weight: bold;
		font-size: 20px;
		color: $color-primary__red;
		margin: 0 0 20px;

		@include max1024{
			font-size: 18px;
			margin-bottom: 10px;
		}
	}

	&__content{
		font-size: 16px;
		line-height: 1.3;

		@include max1024{
			font-size: 14px;
		}

		p{
			line-height: 1.3 !important;
		}

		p + p{
			margin-top: 20px;

			@include max768{
				margin-top: 10px;
			}
		}

		a{
			font-family: $roboto;
		}
	}
}