// settings
@import 'settings/variables';
@import 'settings/media-queries';
@import 'mixins/mixins';

/* --------------------------------------------------- *
 * Base
/* --------------------------------------------------- */
@import "base";
@import 'slider';

/* --------------------------------------------------- *
 * Elements
/* --------------------------------------------------- */
@import 'button';
@import 'form';
@import 'elements/solutions';
@import 'elements/brands';
@import 'elements/techniques';
@import 'elements/casestudy';
@import 'elements/expertise';
@import 'elements/inquiries';

// longtermimpact
.item-longtermimpact{

	@include max1024{
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: 30px;
	}
	&:last-child{
		@include max1024{
			margin-bottom: 0;
		}
	}
	&__box{
		@extend %d_flex;
		@extend %justify_content__center;
		@extend %align_items__stretch;
		flex-flow: row wrap;

		border: 1px solid $color-border--light;
		box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.1);
		max-width: 600px;
		background: linear-gradient(to right, #7a0909 10%, #b80000 90%);

		@include max1024{
			max-width: 100%;
			// padding: 15px;
		}

		.first &{
			margin-left: 0;
			margin-right: auto;
		}
		.last &{
			margin-right: 0;
			margin-left: auto;
		}
	}
	&__image{
		flex: 0 0 220px;
		max-width: 220px;
		order: 2;
		img{
			width: 100%;
			object-fit: cover;
			object-position: center;
			max-height: 362px;
			min-height: 162px;
		}
	}
	&__content{
		flex: 1;
		padding: 30px;

		color: white;
		line-height: 1.4;

		@include max576{
			padding: 15px;
		}
	}

	&__title{
		line-height: 1.25;
		margin: 0 0 5px;
		@include max1024{
			font-size: 21px;
		}
	}

}
.ilt_2{
	.item-longtermimpact{
		&__box{
			max-width: 100%;
			border: none;
			box-shadow: none;
			flex-flow: column wrap;
			@include max1024{
				padding-top: 0;
				padding-bottom: 0;
			}
		}
		&__image{
			flex: 0 0 100%;
			max-width: 100%;
			margin: 0;
		}
		&__content{
			@extend %linear-gradient;
			color: white;
			padding: 60px;

			@include max1024{
				padding: 30px;

				br{
					display: none;
				}
			}
			@include max576{
				padding: 15px;
			}
		}
	}
	&:first-child{
		padding-right: 15px;

		@include max1024{
			padding-right: 0;
		}
	}
	&:last-child{
		padding-left: 15px;
		@include max1024{
			padding-left: 0;
		}
	}
}

// download ebook
.item-downloadebook{
	position: relative;
	text-align: left;
	@include max1024{
		margin-bottom: 30px;
	}
	@include max576{
		flex: 0 0 100%;
		max-width: 100%;
	}
	&--content{
		max-width: 225px;
		border: 1px solid $color-border--light;
		box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.1);

		@include max1024{
			max-width: 100%;
		}
		@include max576{
			max-width: 420px;
		}

		figure{
			margin: 0;
			img{
				@include max1024{
					width: 100%;
				}
			}
		}
		
		&-text{
			padding: 15px;
		}

		h3{
			font-size: 20px;
			font-family: $roboto-light;
			@include max1024{
				font-size: 18px;
			}
		}
		p{
			font-family: $roboto-bold;
		}

		.text-link{
			margin-top: 30px;
			border: none;
			@include max1024{
				margin-top: 0px;
			}
		}
	}
}

.item-director{
	&--box{
		@extend %d_flex;
		flex-flow: row wrap;
		@extend %justify_content__start;
		@extend %align_items__start;

		
		figure{
			margin: 0;
			max-width: 200px;
			flex: 0 0 200px;

			@include max576{
				flex: 0 0 100%;
				max-width: 100%;
				margin-bottom: 30px;
			}
		}
	}
	&--content{
		padding-left: 40px;
		flex: 1;

		@include max576{
			padding-left: 0;
		}

		&--socmed{
			@extend %reset_list;
			text-align: right;
			// margin-top: 60px !important;
			@include max576{
				text-align: left;
				// margin-top: 30px !important;
			}
			li{
				display: inline-block;
				+ li{
					margin-left: 5px;
				}
			}
			.svg-sprite{
				@extend %size_16;
				display: inline-block;
				fill: $color-primary__red;;
			}
			a{
				display: block;
				text-align: center;
				&:hover,
				&:focus{
					opacity: .45;
				}
			}
		}
	}
	&--title{
		font-size: 36px;
		font-weight: bold;
		font-family: $roboto-bold;
		line-height: 1.25;
		@include max768{
			font-size: 21px !important;
		}
		+ .item-director--subtitle{
			margin-top: 0 !important;
			margin-bottom: 15px;
			font-family: $roboto-thin;

		}
	}
	&--subtitle{
		@include max768{
			font-size: 16px !important;
		}
	}
}

.item-article{
	position: relative;
	&.col-md-4{
		@include max768{
			flex: 0 0 50%;
			max-width: 50%;
			margin-bottom: 30px;
		}
		@include max576{
			flex: 0 0 100%;
			max-width: 100%;
		}
	}
	.space{
		@include max768{
			display: none;
		}
	}
	figure{
		margin: 0 0 15px;
		img{
			width: 100%;
			max-height: 180px;
			object-fit: cover;
			object-position: center;
		}
	}
	&--box{
		max-width: 335px;
		@include max576{
			max-width: 420px;
		}
	}
	&--content{
		font-size: 14px;
		@include max576{
			font-size: 12px;
		}
		// font-size: 18px;
		h3{
			font-size: 20px;
			font-family: $roboto-light;
			color: $color-primary__red;
			margin: 0 0 20px;
			@include max1024{
				font-size: 18px;
			}
		}

		.text-link{
			margin-top: 20px;
			display: inline-block;
		}
	}

	&--meta{
		display: block;
		margin-bottom: 5px;
		a{
			font-family: $roboto-light;
			color: $color-text__default;
			font-size: 16px;
			position: relative;
			margin-right: 30px;
			&:after{
				content: "";
				position: absolute;
				right: -15px;
				top: 50%;
				transform: translateY(-50%);
				width: 1px;
				height: 16px;
				background: $color-bg-dark;
				display: block;
			}
			&:last-child{
				margin-right: 0;
				&:after{
					display: none;
				}
			}
			&:hover,
			&:focus{
				opacity: .45;
			}
		}
	}

	&--big{
		.item-article--box{
			max-width: 100%;
			@extend %d_flex;
			@extend %justify_content__start;
			@extend %align_items__start;
			flex-flow: row wrap;

			figure{
				flex: 0 0 45%;
				max-width: 45%;
				@include max576{
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: 30px;
				}
				img{
					width: 100%;
					object-position: center;
					object-fit: cover;
				}
			}
			.item-article--content{
				flex: 1;
				padding-left: 30px;
				@include max576{
					padding-left: 0;
				}
			}
			h3{
				font-size: 20px;
				font-family: $roboto-bold;
				@include max1024{
					font-size: 18px;
				}
			}
		}
	}
}

.item-team{
	position: relative;
	&.col-md-3{
		@include max768{
			flex: 0 0 50%;
			max-width: 50%;
		}
	}
	figure{
		margin: 0 0 20px;
		img{
			width: 80%;
		}
	}
	&--box{
		max-width: 335px;
	}
	&--content{
		font-size: 16px;
		@include max1024{
			font-size: 14px;
		}
		h3{
			font-size: 18px;
			font-family: $roboto-bold;
			margin: 0;
			color: $color-primary__red;
			@include max1024{
				font-size: 16px;
			}
		}
		.position{
			color: $color-primary__red;
		}
		&-socmed{
			@extend %reset_list;
			padding-left: 0 !important;
			padding-top: 10px !important;
			text-align: left;
			li{
				display: inline-block;
				margin: 0 !important;
				+ li{
					margin-left: 5px !important;
				}
			}
			.svg-sprite{
				@extend %size_16;
				display: inline-block;
				fill: $color-primary__red;
			}
			a{
				display: block;
				text-align: center;
				margin: 0;
				border: none !important;
				&:hover,
				&:focus{
					opacity: .45;
				}
			}
		}

	}
}

.item-brand{
	text-align: center;
	&.col-md-3{
		@include max768{
			flex: 0 0 (100%/3);
			max-width: (100%/3);
		}
		@include max576{
			flex: 0 0 50%;
			max-width: 50%;
		}
	}
	&--content{
		display: block;
		img{
			display: inline-block;
		}
	}
}

.item-quote__simple{
	figure{
		margin: 0 0 30px;
	}

	p{
		font-size: 21px;
		line-height: 1.45;
		@include max768{
			font-size: 18px;
		}
	}
	h1{
		font-family: $roboto-bold;
		line-height: 1.2;
		font-size: 23px;
		margin: 0 0 25px;
		
	}
	h3{
		font-family: $roboto-bold;
		margin-top: 30px;
		line-height: 1.2;
		margin-bottom: 0;
	}
}

.item-cta__simple{
	@include max1024{
		border: 1px solid $color-border--light;
		padding: 30px;
		width: 100%;
	}
}

.item-client{
	margin-bottom: 30px;
	&--box{
		text-align: center;
	}
	img{
		max-height: 100px;
		object-fit: cover;
		object-position: center center;

	}
}

// contact
.item-contact-information{
	&.ici_2{
		@extend %d_flex;
		flex-flow: row wrap;
		@extend %justify_content__start;
		@extend %align_items__start;

		+ .ici_2{
			margin-top: 45px;
		}

		.item_content{
			flex: 0 0 210px;
			max-width: 210px;

			@include max768{
				flex: 0 0 100%;
			}

			.item_subcontent{
				padding-left: 20px;
			}
		}
		.item_map{
			position: relative;
			flex: 1;
			@include max768{
				order: -1;
				margin-bottom: 20px;
				max-width: 100%;
				flex: 0 0 100%;
			}
			iframe{
				width: 100%;
			}
		}
	}
}
/* --------------------------------------------------- *
 * Block Content
/* --------------------------------------------------- */
@import "navigation";
@import "footer";
@import "typography";
@import 'section';
.section{
	&-cta{
		&__sep{
			.first{
				padding-right: 45px;
				@include max768{
					padding-right: 15px;
				}
			}
			.last{
				border-left: 2px solid $color-border--light;
				padding-left: 45px;
				@include max1024{
					padding-left: 15px;
					border-left: none;
				}
			}
		}
	}
}
.bg-gray{
	&.section-cta__sep{
		.last{
			border-left-color: rgba( $color-border, .25);
		}
	}
}


@import 'blockquote';

// sidebar
.sidebar-widget{
	&:last-child{
		@include max768{
			margin-bottom: 30px;
		}
	}
	+ .sidebar-widget{
		margin-top: 30px;
		padding-top: 30px;
		border-top: 1px solid $color-border--light;

		@include max768{
			margin-top: 15px;
			padding-top: 15px;
		}
	}
	&--title{
		text-transform: uppercase;
		font-size: 16px;
		font-family: $roboto-light;
		color: $color-text__gray;
		margin: 0;

		@include max768{
			font-size: 14px;
		}

		.arrow-down{
			position: relative;
			width: 8px;
			height: 8px;
			top: -4px;
		}

		&-collapse{
			@include max768{
				cursor: pointer;
			}

			&.active{
				.arrow-down{
					transform: rotate(225deg);
					top: 2px;
				}
			}

			+ .sidebar-widget--content{
				@include max768{
					display: none;
				}
			}
		}

		+ .sidebar-widget--content{
			padding-top: 15px;
		}
	}
}


// controller
.results-controller{
	&--heading{
		padding: 45px;

		@include max768{
			padding: 30px;
		}
		h2{
			color: white;
			font-family: $roboto-light;
			font-size: 1.5rem;

			// @include max1024{
			// 	font-size: 26px;
			// }
		}
	}

	.item-downloadebook{
		@include max1024{
			flex: 0 0 50% !important;
			max-width: 50% !important; 
		}
		@include max576{
			flex: 0 0 100% !important;
			max-width: 100% !important;
		}
		&--content{
			max-width: 100%;
			font-size: 14px;
			@include max576{
				font-size: 12px;
			}
			h3{
				font-size: 16px;
				font-family: $roboto-light;
			}
		}
		figure{
			img{
				width: 100%;
				object-fit: cover;
				object-position: center;
			}
		}
	}
}
.sidebar-controller{
	.form-field{
		+ .form-field{
			margin-top: 10px;
		}
		input[type="checkbox"]{
			+ label{
				&:before{
					bottom: 3px;
				}
				&:after{
					top: 9px;
					@include max576{
						top: 5px;
					}
				}
			}
			&:checked{
				+ label{
					&:after{
						opacity: 1;
						visibility: visible;
					}
				}
			}
		}
	}
}

/* --------------------------------------------------- *
 * Pages
/* --------------------------------------------------- */
@import 'pages/single-page';
@import "pages/home";
@import 'pages/service';

/* --------------------------------------------------- *
 * Extends
/* --------------------------------------------------- */
@import 'extends/extends';


/* --------------------------------------------------- *
 * Helper
/* --------------------------------------------------- */
@import 'helper/helper';

/* --------------------------------------------------- *
 * Media Queries - Typography
/* --------------------------------------------------- */
@include max1024{
	.section-header p,
	.item-expertise p,
	.text-16,
	.section-content,
	.section-divide.sd_2 .btn--transparent,
	.section-divide.sd_2 .section-divide__content_meta a,
	.item-downloadebook--content-text,
	.item-article--content,
	.text-18,
	.item-team--content{
		font-size: 14px !important;
	}
}

@include max576{
	.section-header p,
	.item-expertise p,
	.text-16,
	.section-content,
	.section-divide.sd_2 .btn--transparent,
	.section-divide.sd_2 .section-divide__content_meta a,
	.item-downloadebook--content-text,
	.item-article--content,
	.text-18,
	.item-team--content{
		font-size: 12px !important;
	}
}