.section{
	padding-top: 75px;
	padding-bottom: 75px;
	@include max1440{
		padding-top: 60px;
		padding-bottom: 60px;
	}

	&-header{
		&__title{
			font-family: $roboto-bold;
			font-size: 30px;
			margin: 0 0;

			@include max1024{
				font-size: 26px;
			}

			&-two{
				font-size: 32px;
				@include max1024{
					font-size: 24px;
				}
			}

			&-small{
				font-size: 30px;

				@include max1024{
					font-size: 26px;
				}

				&-two{
					font-size: 28px;

					@include max1024{
						font-size: 24px;
					}
				}
				&-three{
					font-size: 25px;
					font-family: $roboto-light__italic;
					@include max1024{
						font-size: 18px;
					}
				}
			}

			+ p{
				margin-top: 15px;
				@include max1024{
					margin-top: 0;
				}
			}
		}

		&__image{
			@include max1024{
				width: 220px;
			}
		}

		p{
			font-size: 18px;
			&:last-child{
				padding-bottom: 15px;
			}

			@include max1024{
				font-size: 16px;
			}
			@include max768{
				font-size: 14px;
			}
		}

		+ .section-content{
			margin-top: 60px;
			@include max1024{
				margin-top: 45px;
			}
			@include max576{
				margin-top: 30px;
			}
		}
		+ .sc_2{
			margin-top: 30px;
			@include max1024{
				margin-top: 15px;
			}
		}
	}

	&-content{
		font-size: 20px;

		@include max1024{
			font-size: 16px;
		}
		@include max768{
			font-size: 14px;
		}
	}
	&-divide{
		padding: 0;
		p{
			line-height: 1.75;
		}

		.col-7,
		.col-5{
			@include max1024{
				flex: 0 0 100%;
				max-width: 100%;
			}
		}
		.col-7{
			@include max1024{
				min-height: 570px;
			}
			@include max768{
				min-height: 380px;
			}
		}
		.col-5{
			@include max1024{
				order: 2;
			}
		}
		&__content{
			padding: 35px 70px 80px 80px;
			min-height:  775px;
			display: flex;
			flex-flow: column nowrap;
			justify-content: center;
			align-items: center;

			@include max1440{
				min-height: initial;
				padding-left: 45px;
				padding-right: 45px;
			}
			@include max1024{
				padding-top: 45px;
				padding-bottom: 45px;
			}
			@include max768{
				padding-left: 15px;
				padding-right: 15px;
			}

			.section{
				&-header,
				&-content{
					flex: 0 0 100%;
					max-width: 100%;
					width: 100%;
				}
				&-header{
					margin-bottom: 30px;

					// @include max1024{
					// 	margin-bottom: 45px;
					// }
					// @include max768{
					// 	margin-bottom: 30px;
					// }

					+ .section-content{
						margin-top: 0;
					}
				}
			}

			.btn{
				&--primary{
					color: white;
					border: 2px solid #cc8080 !important;
					background-color: $color-primary__red;
					font-weight: bold;
					&:hover,
					&:focus{
						color: $color-primary__red !important;
						background: white;
						border-color: white !important;
					}

				}
			}
		}

		&.sd_2{
			.section-header{
				&__title{
					line-height: 1.2;
				}

				+ .section-content{
					margin-top: 15px;
					@include max1024{
						margin-top: 0;
					}
				}
			}
			.section-divide{
				&__content{
					@include max1024{
						padding-left: 20px;
						padding-right: 20px;
					}
					@include max768{
						padding-left: 5px;
						padding-right: 5px;
					}
					&_meta{
						display: block;
						margin-bottom: 15px;
						@include max1024{
							margin-bottom: 0;
						}
						a{
							color: white;
							font-size: 14px;
							position: relative;

							margin-right: 20px;
							display: inline-block;
							&:after{
								content: "";
								position: absolute;
								height: 16px;
								width: 1px;
								background: white;
								right: -10px;
								top: 6px;
							}

							&:last-child{
								&:after{
									display: none;
								}
							}
							&:hover,
							&:focus{
								color: $color-white--light;
							}
						}
					}
				}
			}

			.btn--transparent{
				position: absolute;
				right: 40px;
				bottom: 40px;
				color: white;
				font-size: 18px;
				font-family: $roboto-medium;

				@include max1024{
					position: relative;
					padding: 0;
					right: initial;
					bottom: 0;

				}
			}
		}
		&.sd_3{
			.section-divide{
				&__content{
					padding-top: 130px;
					min-height: 400px;

					@include max1024{
						min-height: initial;
					}
				}
			}
		}
		// make this class support for background image placement
		&.sd_4{
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center center;
		}
	}
	&-bg{
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		padding: 100px 0;
		position: relative;

		&--mask{
			position: absolute;
			height: 100%;
			width: 100%;
			background: white;
			opacity: .75;
			top: 0;
			left: 0;
		}
		&--content{
			position: relative;
		}
		
		&--float{
			&-content{
				max-width: 645px;
				padding: 60px;
				margin-left: auto;
				margin-right: 40px;

				@include max1024{
					margin-right: auto;
					text-align: center;
					padding: 45px;
				}
				@include max576{
					padding: 15px;
				}
			}

			&.sbf_2{
				padding-top: 90px;
				padding-bottom: 90px;
			}
		}
	}

	&-brand{
		padding-bottom: 90px;
		@include max1024{
			padding-bottom: 75px;
		}
		.custom-nav{
			&__prev,
			&__next{
				height: 38px;
				width: 38px;
				line-height: 30px;
				text-align: center;
				@include max1024{
					border: 1px solid rgb(88, 89, 91);
					height: 32px;
					width: 32px;
					text-align: center;
					line-height: 26px;
					background: white;
				}
			}
			&__icon{
				
			}
		}
	}
	&-data-work{
		.item-expertise{
			@include max768{
				flex: 0 0 100%;
				max-width: 100%;
			}

			+ .item-expertise{
				@include max768{
					margin-top: 45px;
				}
			}
		}
	}
	&-contact{
		&__form{
			@include max768{
				margin-top: 45px;
			}
			h2{
				@include max768{
					text-align: left !important;
				}
			}
		}
	}
}