@include selection{
	background: $color-primary__red;
	color: white;
}

body, html{
	height: 100%;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}
body{
	font-family: "Roboto", sans-serif;
	font-size: 15px; 
	line-height: 1.65;

	@include max1024{
		font-size: 14px;
	}
	@include max576{
		font-size: 12px;
	}
}

.container{
	&.c2{
		width: 100%;
		// padding-left: 20px;
		// padding-right: 20px;

		@media (min-width: 576px){
			max-width: calc(100%);
		}
		@media (min-width: 768px){
			max-width: calc(100% - 30px);
		}
		@media (min-width: 992px){
			max-width: calc(100% - 30px);
		}
		@media (min-width: 1200px){
			max-width: 1140px;
		}
		@media (min-width: 1420px){
			max-width: 1400px;
		}
		
		.row{
			// margin-left: -20px;
			// margin-right: -20px;
		}

		.col, [class*='col-'], [class*='col-']{
			// padding-left: 20px;
			// padding-right: 20px;			
		}
	}
	&-fluid{
		&.no-gutters{
			padding-left: 0 !important;
			padding-right: 0 !important;
		}
	}
}
.no-gutters{
	margin-left: 0 !important;
	margin-right: 0 !important;
}

li{
  list-style: none;
}

// Typography
h1, h2, h3, h4, h5, h6{
	line-height: 1.55;
	@include max576{
		line-height: 1.25;
	}
}
h1{}
.h1{
	font-size: 36px;
	@include max1024{
		font-size: 28px;
	}
}

h2{
	&.h2_v2{
		font-size: 24px;
		@include max768{
			font-size: 21px;
		}
	}
}
.h2{
	font-size: 28px;
	@include max768{
		font-size: 23px;
	}
	&-small{
		font-size: 22px;
		@include max768{
			font-size: 18px;
		}
	}
}
h3{}
.h3{
	font-size: 20px;
	@include max768{
		font-size: 18px;
	}
	&-small{
		font-size: 18px;
		@include max768{
			font-size: 16px;
		}
	}
}
h4{}
.h4{}
h5{}
.h5{}
h6{}
.h6{}
p{
	margin-bottom: 0;
	+ p{
		margin-top: 20px;
		@include max1024{
			margin-top: 15px;
		}
	}
}

a{
	text-decoration: none;
	@extend %transition-default;
	&:hover,
	&:focus{
		text-decoration: none;
	}
}

// Images
img {
  max-width: 100%;
}

// List
.list{
	&-dot{
		padding: 0;
		margin: 0;

		li{
			position: relative;
			&:before{
				content: '';
				position: relative;
				height: 5px;
				width: 5px;
				border-radius: 50%;
				background: $color-bg-dark;
				display: inline-block;
				top: -2px;
				margin-right: 10px;
			}
		}

		&.ld_2{
			list-style: none outside none;
			li{
				list-style-type: none;
				display: block;

				+ li{
					margin-top: 20px;
				}
				&:before{
					position: absolute;
					left: -15px;
					top: 10px;
					background: $color-primary__red;
				}
			}
		}
	}
	&-vacancies{
		list-style: none outside none;
		margin: 0 !important;
		padding: 0 !important;
		li{
			list-style-type: none !important;
		}
	}
}

// separator
.sep-content{
	.sep{
		height: 2px;
		width: 100%;
		background: $color-border--light;
		display: block;
	}
}

.have-icon{
	&__left{
		.svg-sprite{
			display: inline-block;
			margin-right: 5px;
		}
	}
	&__right{
		.svg-sprite{
			display: inline-block;
			margin-left: 5px;
		}
	}
}