.item-casestudy{
	@include max1024{
		flex: 0 0 50%;
		max-width: 50%;
		margin-bottom: 30px;
	}
	@include max580{
		flex: 0 0 100%;
		max-width: 100%;
	}
	&__box{
		border-top: 15px solid $color-primary__red;
		background: $color-bg-white;
		padding: 15px;
	}

	&__header{
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
		min-height: 275px;
		padding: 15px 20px;
		margin-bottom: 15px;

		&-title{
			font-size: 1.4rem;
			font-family: $roboto-bold;
			font-weight: bold;
			text-transform: uppercase;
			color: white;
			display: inline-block;
			a{
				color: white !important;
				padding-bottom: 3px;
				border-bottom: 2px solid white !important;
				display: block;
				transition: all 350ms ease;

				@include max1024{
					border-bottom-width: 1px;
				}
			}
		}
	}

	&__content{
		padding: 5px 20px;
		border-left: 2px solid $color-primary__red;
		font-family: $roboto-thin;
		font-size: 28px;
		line-height: 1.5;
		font-weight: 300;
		color: $color-primary__red;

		p{
			font-family: $roboto-light !important;
		}

		@include max1024{
			font-size: 18px;
		}
	}

	&__small{
		background: $color-bg-white;
		border-top: 15px solid $color-primary__red;

		+ .item-casestudy__small{
			margin-top: 30px;

			@include max1024{
				margin-top: 0;
			}
		}

		.item-casestudy{
			&__header{
				min-height: initial;
				padding-bottom: 0;

				&-title{
					color: $color-primary__red;
					font-size: 23px;
					text-transform: none;
					display: block;
					border: none;
					margin: 0;
					padding: 0;

					@include max1024{
						font-size: 16px;
					}
				}
			}
			&__content{
				border-left: 0;
				padding-top: 0;
				padding-bottom: 20px;
				font-size: 12px;
				font-weight: normal;
				line-height: 1.25;
				color: $color-text__default;
				
				p{
					font-family: $roboto;
				}
			}
		}
	}
}

.section{
	&-content{
		&__case-study{
			display: flex;
			align-items: stretch;

			.item-casestudy{
				&__box{
					height: 100%;
				}
			}
		}
	}
}

.grouping-expertise{
	align-items: stretch;
	flex-flow: row wrap;
	justify-content: flex-start;

	.item-casestudy{
		@include max1024{
			flex: 0 0 100%;
			max-width: 100%;
		}
		&__box{
			height: 100%;
		}
	}
	+ .grouping-expertise{
		margin-top: 30px;
	}

	.list-casestudy{
		display: flex;
		flex-flow: column nowrap;
		align-items: stretch;

		@include max1024{
			flex: 0 0 100%;
			max-width: 100%;
			align-items: flex-start;
			flex-flow: row wrap;
			justify-content: space-between;
		}


		.item-casestudy__small{
			height: 100%;

			@include max1024{
				height: initial;
				flex: 0 0 48%;
				max-width: 48%;
			}

			@include max580{
				flex: 0 0 100%;
				max-width: 100%;
			}

			&.last{
				@include max1024{
					flex: 0 0 100%;
					max-width: 100%;
				}
			}
		}
	}
}