.home-header {
  position: relative;
  width: 100%;
  // height: 100vh;
  padding-top: 20%;
  padding-bottom: 17%;
  background-image: url("./../images/home-bg_2@4x.png");
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;

  @include max576{
    padding-top: 30%;
    padding-bottom: 20%;
  }

  .space-30{
    @include max768{
      display: none;
    }
  }

  .container-fluid {
    // position: absolute;
    // top: 49%;
    // left: 50%;
    // transform: translate(-50%, -50%);
  }

  &__title {
    text-align: center;
    color: #fff;

    @include max1024{
      margin-bottom: 0;
    }

    span {
      display: block;
      line-height: 1;
      font-family: "Roboto-Light";
    }

    &--first {
      font-size: 56px;
      margin-bottom: 25px;

      @include max1024{
        font-size: 48px;
        margin-bottom: 15px;
      }
      @include max768{
        font-size: 34px;
      }
    }

    &--second {
      font-size: 28px;
      font-weight: 200;
      margin-bottom: 25px;

      @include max1024{
        font-size: 24px;
      }
      @include max768{
        font-size: 18px;
      }
    }
  }

  &__bg {
    position: absolute;
    top: -30px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
  }

  .btn{
    font-family: $roboto-medium;
  }
}

.home-expertise {
  padding-top: 50px;
  padding-bottom: 50px;

  .section-title {
    text-align: center;
    color: #cb0000;
    margin-bottom: 20px;
  }

  .section-paragraph {
    font-size: 20px;
    text-align: center;
    margin-bottom: 40px;
  }

  .expertise {
    &__img {
      padding-right: 25%;
      margin-bottom: 15px;
    }

    &__title {
      line-height: 1;
      font-size: 26px;
      color: #cb0000;
      margin-bottom: 20px;
    }

    &__paragraph {
      font-size: 16px;
    }
  }

  .btn-row {
    margin-top: 10px;
  }

  .col {
    padding: 0 3%;
  }
}

.home-case {
  .col-5 {
    background-color: #cb0000;
    background: linear-gradient(to right, #7a0909, #b80000);
    padding: 0px 6%;
    padding-top: 100px;
    padding-bottom: 130px;
  }

  &__logo {
    width: 66%;
    margin-bottom: 40px;
  }

  &__paragraph {
    color: #fff;
    font-family: "Roboto-Light";
    margin-bottom: 80px;
    line-height: 2;
  }

  .btn {
    margin-left: 0;
  }

  .col-7 {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    padding: 0;
  }
}

.home-brand {
  padding-bottom: 100px;
  .brand-slider{
    padding: 0 5%;
    margin: 0 5%;
  }
  .brand-slide {
    width: 25%;
  }

  .section-title {
    margin: 70px 0;
    text-align: center;
  }
}
