.item-expertise{
	&__box{
		padding-left: 15px;
		padding-right: 15px;
		text-align: center;

		// @include max768{
		// 	text-align: center;
		// }
	}

	&__image{
		max-width: 180px;
		margin: 0 auto 30px;

		@include max768{
			margin-left: auto;
			margin-right: auto;
		}
	}

	&__title{
		font-family: $roboto-light;
		font-size: 22px;
		color: $color-primary__red;
		margin: 0 0 20px;

		@include max1024{
			font-size: 21px;
			margin: 0 0 10px;
		}
		@include max768{
			margin-bottom: 5px;
		}
	}
	// p{
		// font-size: 18px;
	// }
	.btn--secondary{
		background: transparent;
		margin-top: 20px;

		@include max768{
			margin-top: 0;
		}
		&:hover,
		&:focus{
			background: $color-primary__red;
		}
	}
}