.space{
	display: block;
	width: 100%;
	position: relative;

	&-55{
		height: 55px;
	}
	&-45{
		height: 45px;
	}
	&-40{
		height: 40px;
	}
	&-30{
		height: 30px;
	}
	&-15{
		height: 15px;
	}
	&-20{
		height: 20px;
	}
	&-10{
		height: 10px;
	}
	&-5{
		height: 5px;
	}

	&-section{
		height: 75px;

		@include max768{
			height: 45px;
		}

		&__first{
			height: 90px;
			@include max768{
				height: 75px;
			}
		}

		&.ss_2{
			// space section v2
			height: 45px;
			@include max768{
				height: 30px;
			}
		}
	}
}
.nospace{
	&-top{
		padding-top: 0px;
		margin-top: 0px;
	}
	&-bottom{
		padding-bottom: 0px;
		margin-bottom: 0px;
	}
}