.single-page{
	padding-top: 60px;
	padding-bottom: 95px;
	overflow: hidden;

	@include max768{
		padding-bottom: 45px;
	}
	&__divider{
		@include max1024{
			padding-bottom: 0;
		}
		.content_divider{
			padding: 60px 90px;

			@include max1280{
				padding: 30px 45px;
			}
			@include max1024{
				flex: 0 0 100%;
				max-width: 100%;
			}
			@include max576{
				padding-left: 15px;
				padding-right: 15px;
			}
		}
		.single-page__header{
			margin-bottom: 45px;
		}

		&-overlap{
			position: relative;
			background: $color-bg-white;
			&:before,
			&:after{
				content: "";
				position: absolute;
				background: $color-bg-white;
				height: 50vw;
				width: 100%;
				left: 0;
				z-index: -1;

				@include max1024{
					display: none;
				}
			}
			&:before{
				bottom: 100%;
			}
			&:after{
				top: 100%;
			}

		}
	}
	// header
	&__header{
		margin-bottom: 60px;
		font-family: $roboto;

		p{
			font-family: $roboto;
		}

		@include max1024{
			margin-bottom: 35px;
		}
		&-meta{
			a{
				color: $color-primary__red;
				&:hover,
				&:focus{
					opacity: .45;
				}
			}
		}
		&-title,
		&-subtitle{
			color: $color-primary__red;
		}

		&-title{
			margin: 0;
			font-weight: bold;
			font-size: 36px;

			@include max1024{
				font-size: 28px;
			}

			&-small{
				font-size: 38px;

				@include max1024{
					font-size: 28px;
				}

				&-two{
					font-size: 36px;
					@include max1024{
						font-size: 26px;
					}
				}
				&-three{
					font-size: 32px;
					@include max1024{
						font-size: 26px;
					}
				}
			}
			&-smallest{
				font-size: 30px;
				@include max1024{
					font-size: 26px;
				}
				&.stst_2{
					font-size: 25px;
					@include max1024{
						font-size: 21px;
					}
				}
				&.stst_3{
					font-size: 28px;
					@include max1024{
						font-size: 21px;
					}
				}
				&.stst_4{
					font-size: 24px;
					@include max1024{
						font-size: 21px;
					}
				}
			}


			+ .single-page__header-subtitle{
				margin-top: 15px;

				@include max1024{
					margin-top: 5px;
				}
			}
		}
		&-subtitle{
			margin: 0;
			font-family: $roboto-light__italic;
			font-size: 25px;

			@include max1024{
				font-size: 20px;
			}
			&.sphs_2{
				font-size: 18px;
				@include max1024{
					font-size: 16px;
				}
			}
		}

		// layout model 
		&-bg{
			background-repeat: no-repeat;
			background-position: center center;
			background-size: cover;

			min-height: 100vh;

			@include max1024{
				padding-top: 230px;
				min-height: 75vh;
			}
		}

		&-float{
			min-height: initial;
			padding-top: 220px;
			padding-bottom: 95px;
			position: relative;

			@include max1024{
				min-height: 580px;
			}
			.container-fluid{
				height: 100%;
				// position: relative;
			}

			&-content{
				@extend %linear-gradient;
				width: 645px;
				padding: 60px;
				margin-left: auto;
				margin-right: 40px;

				@include max1024{
					max-width: 100%;
					position: absolute;
					left: 0;
					bottom: 0;
					width: 100%;
					margin: 0;
					padding: 45px;
				}
				@include max576{
					padding: 15px;
				}

				.single-page__header-title{
					color: white;
					font-family: $roboto-light;
					font-weight: initial;
					line-height: 1.25;
				}
			}
		}

		&-box{
			position: relative;

			&-content{
				margin-left: 10px;
				border: 1px solid $color-border--light;
				padding: 30px;
				@include max1024{
					margin: 0 0 15px;
				}
				@include max576{
					padding: 15px;
				}
				&-title{
					font-size: 20px;
					font-family: $roboto-light;
					color: $color-primary__red;
				}
				.text-right{
					@include max1024{
						text-align: left !important;
					}
				}
			}

			&-sep{
				border-top: 1px solid $color-border--light;
				margin-top: 30px;
				margin-bottom: 30px;
				@include max1024{
					display: none;
				}
			}

			&-categories{
				@extend %reset_list;
				@extend %d_flex;
				@extend %justify_content__start;
				@extend %align_items__start;
				flex-flow: row wrap;
				li{
					font-size: 16px;
					font-weight: bold;
					display: block;
					padding-right: 15px;
					margin-right: 15px;
					position: relative;

					@include max768{
						font-size: 14px;
					}
					&:last-child{
						padding-right: 0;
						margin-right: 0;
						&:after{
							display: none;
						}
					}
					&.active,
					&:hover,
					&:focus{
						a{
							color: $color-primary__red;	
						}
						
					}
					a{
						color: $color-text__gray;
					}

					&:after{
						content: "";
						position: absolute;
						height: 21px;
						width: 2px;
						background: $color-border--light;
						right: 0;
						top: 50%;
						transform: translateY(-50%);
					}
				}
			}
		}

		&-divide-content{
			position: relative;
			overflow: hidden;

			.col-5{
				position: relative;
				background: rgba( $color-bg-white, 0.7);
				min-height: 100vh;

				@include max1024{
					flex: 0 0 100%;
					max-width: 100%;
					min-height: initial;
				}

				&:after,
				&:before{
					content: '';
					position: absolute;
					top: 0;
					height: 100%;
					width: 100vw;
					background: rgba( $color-bg-white, 0.7);
				}
				&:before{
					right: 100%;
					@include max1024{
						top: 100%;
						left: 0;
						right: initial;
					}
				}
				&:after{
					left: 100%;
					display: none;

					@include max1024{
						display: block;
					}
				}
			}

			&-wrapper{
				padding: 120px 70px 120px 70px;
				min-height: 775px;

				font-size: 16px;
				line-height: 2;
				color: $color-text__default;

				display: flex;
				flex-flow: column nowrap;
				justify-content: center;
				align-items: flex-start;

				@include max1280{
					min-height: initial;
					padding: 45px;
					padding-top: 145px;
				}

				@include max1024{
					font-size: 16px;
					padding: 30px 0;
				}
				@include max768{
					font-size: 14px;
				}

				&:after,
				&:before{
					content: '';
					position: absolute;
					top: 0;
					height: 100%;
					width: 100vw;
					background: rgba( $color-bg-white, .5);
				}
				&:before{
					right: 100%;
				}
				&:after{
					left: 100%;
					display: none;

					@include max1024{
						display: block;
					}
				}

				p{
					max-width: 85%;
				}

				.single-page{
					&__header{
						&-title,
						&-image{
							margin-bottom: 45px;
							
							@include max1024{
								margin-bottom: 15px;
							}
						}
						&-image{
							width: 325px;
							margin-bottom: 20px;
							@include max1024{
								width: 280px;
							}
							@include max768{
								width: 190px;
							}

							&.british-airways{
								position: relative;
								margin-left: -15px;
							}
							&.hs2{
								width: 275px;
								position: relative;
								margin-left: 45px;

								@include max1024{
									margin-left: 20;
								}
							}
						}
					}
				}
			}

			&-red{
				.col-5{
					background: linear-gradient(to right, #7a0909, #b80000);
				}	
				// .single-page__header{
				// 	&-divide-content-wrapper{
				// 		padding-right: 70px;
				// 	}
				// }
			}
		}

		&-separator{
			.separator{
				border-top: 2px solid $color-border--light;
				margin-top: 30px;
				margin-bottom: 60px;
			}

			&.sphs_2{
				.separator{
					margin-bottom: 30px;
				}
			}

			&-content{
				@extend %d_flex;
				flex-flow: row wrap;
				@extend %justify_content__start;
				@extend %align_items__start;

				@include max576{
					display: block !important;
				}
				&:before,
				&:after{
					@include max576{
						content: '';
						display: table;
						clear: both;
					}
				}

				figure{
					flex: 0 0 75px;
					max-width: 75px;
					margin: 0;

					@include max576{
						float: left;
						margin-right: 15px;
						margin-bottom: 15px;
					}
				}
				.content{
					flex: 1;
					padding-left: 15px;

					@include max576{
						padding-left: 0;
					}
				}
			}
		}

		&-casestudy{
			.single-page__header-divide-content-wrapper{
				line-height: 1.75;
			}

			&-britishairways{
				.col-5{
					background: rgba( white, .75 );
					&:before{
						background: rgba( white, .75 );
					}
				}
			}
		}
	}

	// footer
	&__footer{
		margin-top: 45px;
	}

	&__share{
		text-align: right;
		&_title{
			font-family: $roboto-bold;
			font-size: 16px;
			color: $color-primary__red;
			display: inline-block;
			margin: 0 0 0;
			margin-right: 15px;
		}
		&_list{
			@extend %reset_list;
			display: inline-block;
			position: relative;
			top: -3px;
			li{
				display: inline-block;
				&:first-child{
					margin-left: 0;
				}
				+ li{
					margin-left: 5px;
				}
			}
			.svg-sprite{
				@extend %size_16;
				display: inline-block;
				fill: $color-primary__red;;
			}
			a{
				display: block;
				text-align: center;
				&:hover,
				&:focus{
					opacity: .45;
				}
			}
		}
	}

	&__solutions,
	&__service{
		padding-bottom: 0;
	}

	&__contact{
		
		&-business-inquiries{
			background: rgba($color-bg-gray, .25);

			&-title{
				margin: 0;
				background: $color-primary__red;
				padding: 10px 15px;
				font-size: 23px;
				color: white;

				@include max1024{
					font-size: 18px;
					margin-bottom: 15px;
				}
				@include max768{
					font-size: 16px;
				}
			}
		}

		// DEPFRECATED
		// @since v2 of design
		// ----
		// &-bg{
		// 	background-repeat: no-repeat;
		// 	background-size: cover;
		// 	background-position: center center;

		// 	background-image: url('../images/home-bg_2@4x.png');

		// 	min-height: 715px;
		// }
		
		// DEPFRECATED
		// @since v2 of design
		// ----
		// &-wrapper{
		// 	max-width: 90%;
		// 	margin-top: -575px;
		// 	margin-left: auto;
		// 	margin-right: auto;
		// 	padding-top: 95px;
		// 	padding-bottom: 110px;
		// 	background: white;

		// 	border-radius: 22px;
		// 	box-shadow: 0 5px 15px rgba(black, .15);

		// 	@include max1024{
		// 		border-radius: 12px;
		// 		padding: 60px 20px 75px;
		// 	}

		// 	.offset-md-1{
		// 		@include max1024{
		// 			margin: 0;
		// 		}
		// 	}
		// 	.col-md-10{
		// 		@include max1024{
		// 			flex: 0 0 100%;
		// 			max-width: 100%;
		// 		}
		// 	}
		// }
	}

	&__navigation{
		margin: 0 0 60px;

		&-content{
			display: flex;
			flex-flow: row wrap;
			justify-content: space-between;
			align-items: center;

			a{
				display: inline-block;
				font-size: 16px;
				color: $color-primary__red;
				text-transform: uppercase;

				@include max1024{
					font-size: 14px;
				}

				&:hover,
				&:focus{
					opacity: .65;
				}

				span{
					display: inline-block;
				}

				&.prev{
					span{
						margin-left: 5px;
					}
				}
				&.next{
					span{
						margin-right: 5px;
					}
				}
			}
		}

		&-icon{
			width: 21px;
			height: 12px;
			display: inline-block;
			vertical-align: middle;
			position: relative;
			top: -2px;
		}
	}

	&__header{
		margin-bottom: 45px;
		@include max768{
			margin-bottom: 30px;
		}
		&-title{
			line-height: 1.25;
			font-family: $roboto-bold;
		}
		&-categories{
			a{
				text-transform: uppercase;
				color: $color-text__default;
				font-size: 18px;

				@include max768{
					font-size: 16px;
				}
				&:hover,
				&:focus{
					opacity: .45;
				}
			}
		}
		&-meta{
			margin-top: 30px;
			padding-top: 15px;
			border-top: 1px solid $color-border--light;
			font-size: 18px;
			@include max768{
				font-size: 16px;
				margin-top: 15px;
			}
			p + p{
				margin-top: 5px;
			}
			&-author{
				a{
					color: $color-primary__red;
				}
			}
			&-date{
				a{
					color: $color-text__default;
				}
			}
			a{
				&:hover,
				&:focus{
					opacity: .45;
				}
			}

			&.sphm_2{
				margin-top: 0;
				padding-top: 0;
				border-top: none;
				@include max768{
					margin-top: 0;
				}
			}
		}
	}
	// content
	&__content{
		color: $color-text__default;
		font-family: $roboto;
		position: relative;
		@extend %clearfix;
		// font-size: 15px;

		&.spc_2{
			font-size: 14px;
			@include max576{
				font-size: 12px;
			}
		}

		.align{
			&-left{
				float: left;
				margin-right: 30px;
				margin-bottom: 30px;
			}
			&-right{
				float: right;
				margin-bottom: 30px;
				margin-left: 30px;
			}
			&-center{
				display: block;
				margin-bottom: 30px;
				text-align: center;
			}
		}
		ul, ol{
			padding-left: 20px;
			@include max1024{
				padding-left: 14px;
			}
		}
		ul{
			list-style: disc;
			li{
				list-style-type: initial;
			}
		}
		ol{
			li{
				list-style-type: decimal-leading-zero;
			}
		}
		&-small-text{
			font-weight: 300;
			font-size: 16px;
 
			p, ul, ol{
				font-family: $roboto-light;
				line-height: 1.75;
			}
			a{
				border-bottom-width: 1px !important;
			}
		}
		&-smaller-text{
			font-weight: 300;
			font-size: 14px;

			p, ul, ol{
				font-family: $roboto-light;
				line-height: 1.75;
			}
			a{
				border-bottom-width: 1px !important;
			}
		}

		@include max1024{
			font-size: 16px;
		}
		@include max768{
			font-size: 14px;
		}
		@include max576{
			font-size: 12px;
		}

		&-title{
			font-family: $roboto-bold;
			font-weight: bold;
			font-size: 38px;
			color: $color-primary__red;
			margin: 0 0 60px;

			@include max1024{
				font-size: 21px;
				margin-bottom: 15px;
			}
			@include max768{
				font-size: 18px;
			}

			&.text-center{
				max-width: 800px;
				margin-left: auto;
				margin-right: auto;
			}


			&-small{
				font-size: 30px;
			}

			&-smaller{
				font-size: 24px;
				font-family: $roboto-bold;
				font-weight: bold;
				color: $color-primary__red;
				margin: 0 0 30px;
				@include max1024{
					font-size: 21px;
					margin-bottom: 15px;
				}
				@include max576{
					font-size: 18px;
				}
			}
		}

		&-subtitle{
			font-family: $roboto-bold;
			font-weight: bold;
			font-size: 26px;
			color: $color-primary__red;
			margin: 0 0 20px;

			@include max1024{
				font-size: 18px;
				margin-bottom: 15px;
			}
			@include max768{
				font-size: 16px;
			}

			&.text-center{
				max-width: 800px;
				margin-left: auto;
				margin-right: auto;
			}
		}

		a{
			display: inline-block;
			color: $color-text__default;
			border-bottom: 2px solid $color-primary__red;

			@include max1024{
				border-bottom-width: 1px;
			}

			&.text-white{
				border-bottom-color: white;
			}

			&:hover,
			&:focus{
				opacity: .65;
			}
		}

		&-with-sidebar{
			.col-md-8{
				padding-right: 45px;

				@include max1024{
					flex: 0 0 100%;
					max-width: 100%;
				}
			}

			.col-md-4{
				@include max1024{
					flex: 0 0 100%;
					max-width: 100%;
				}
			}
		}
	}
}