.solutions{
	&__wrapper{
		display: flex;
		flex-flow: column nowrap;
		justify-content: flex-start;
		align-items: flex-start;
		margin-top: 45px;
		margin-bottom: 45px;
		padding-left: 60px;
	}

	&__item{
		font-size: 16px;

		@include max768{
			font-size: 14px;
		}
		+ .solutions__item{
			margin-top: 45px;

			@include max1024{
				margin-top: 30px;
			}
		}
	}

	&__title{
		color: $color-primary__red;
		font-size: 20px;
		font-family: $roboto-bold;
		font-weight: bold;
		margin: 0 0 15px;
		// max-width: 335px;
		position: relative;

		@include max1024{
			font-size: 18px;
		}
		@include max768{
			padding-left: 20px;
			margin-bottom: 15px;
		}

		&:before{
			content: '';
			position: absolute;
			top: 8px;
			left: -20px;

			width: 10px;
			height: 10px;
			border-radius: 50%;
			background-color: $color-primary__red;
			display: block;

			@include max1024{
				width: 7px;
				height: 7px;
				left: -15px;
				top: 8px;
			}
			@include max768{
				left: 0;
			}
		}
	}
}