.item-techniques{
	margin-bottom: 30px;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
	align-items: stretch;

	@include max1024{
		flex: 0 0 50%;
		max-width: 50%;
	}
	@include max380{
		flex: 0 0 100%;
		max-width: 100%;
	}

	p{
		font-family: $roboto;
		font-weight: normal;
		font-size: 12px;
		line-height: 1.25;
	}

	&__content{
		border: 2px solid $color-primary__red;
		padding: 15px;
		height: 100%;
	}

	&__header{
		display: flex;
		flex-flow: row nowrap;
		justify-content: flex-start;
		align-items: flex-start;
		margin-bottom: 10px;

		@include max1024{
			align-items: flex-end;
		}

		&-icon{
			width: 30px;
			height: 30px;
			margin-right: 15px;

			@include max1024{
				width: 24px;
				height: 24px;
				margin-right: 10px;
			}
		}

		&-title{
			color: $color-primary__red;
			margin: 0;
			font-size: 23px;
			font-family: $roboto-bold;
			font-weight: bold;

			@include max1024{
				font-size: 18px;
			}
		}
	}
}