%arrow{
  height: 10px;
  width: 10px;
  display: inline-block;
  border-bottom: 1px solid white;
  border-right: 1px solid white;
  display: inline-block;
  margin-left: 10px;

  @include max576{
    height: 7px;
    width: 7px;
  }
}
.arrow{
  &-right{
    @extend %arrow;
    transform: rotate(-45deg);
  }
  &-down{
    @extend %arrow;
    border-bottom-color: $color-bg-gray;
    border-right-color: $color-bg-gray;
    transform: rotate(45deg);
  }
}

.btn{
  border-radius: 0;
  font-family: $roboto-light;
  font-size: 14px;
  outline: none;

  padding: 12px 28px;

  @include max1024{
    font-size: 13px;
    padding: 8px 21px;
  }
  @include max768{
    font-size: 12px;
  }

  &--primary{
    color: white !important;
    border: 2px solid #cc8080;
    background-color: $color-primary__red;
    &:hover,
    &:focus{
      color: $color-primary__red !important;
      background: white;
      border-color: white;
    }

    &.btp-2{
      border: none !important;
      &:hover,
      &:focus{
        background: $color-primary__red-dark;
        color: white !important;
      }
    }
  }

  &--secondary{
    color: $color-primary__red;
    border: 2px solid $color-primary__red;
    background: rgba( $color-bg-white, .75);

    &.bts-2{
      background: transparent;

      &:hover,
      &:focus{
        color: white;
        background: $color-primary__red;
      }
    }

    &:hover,
    &:focus{
      color: white;
      background: $color-primary__red;
    }
  }

  &_line{
    @extend %reset-border-radius;
    &__primary{
      border: 1px solid $color-primary__red;
      background: transparent;
      color: $color-primary__red !important;

      &:hover,
      &:focus{
        background: $color-primary__red;
        color: white !important;
      }


    }
    &__white{
      border: 1px solid white !important;
      background: transparent;
      color: white;

      &:hover,
      &:focus{
        background: transparent;
        color: white;
        opacity: .65;
      }
    }
  }
  &--transparent{
    background: transparent;

    &:hover,
    &:focus{
      border: none;
      outline: none !important;
      opacity: .45;
    }
  }
  &-has--icon{
    @extend %clearfix;
  }

  &-small{
    padding: 6px 21px;
  }
}

.text-link{
  &--primary{
    color: $color-primary__red !important;
    &:hover,
    &:focus{
      opacity: 0.45;
      color: $color-primary__red !important;
    }

    &.tlp_2{
      border-bottom: 1px solid $color-primary__red;
    }
    
    .arrow-right{
      border-bottom: 1px solid $color-primary__red;
      border-right: 1px solid $color-primary__red;
    }
  }
  &--black{
    color: $color-text__default !important;
    &:hover,
    &:focus{
      opacity: 0.45;
      color: $color_text__default !important;
    }

  }
  &--has-icon{
    .arrow-right{
      margin-left: 5px;
    }
  }
}