.footer {
  background: linear-gradient(to right, #7a0909, #b80000);

  &.f2{
    padding: 90px 0 30px;
    color: white;
    @include max576{
      padding: 60px 0 30px;
    }
  }

  .container-fluid {
    padding-left: 8%;
    padding-right: 5%;
    padding-top: 40px;
    padding-bottom: 25px;
    color: #fff;

    @include max768{
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  .row{
    .col-5{
      @include max1024{
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 30px;
      }
    }
    .col-3{
      @include max1024{
        flex: 0 0 (100%/3);
        max-width: (100%/3);
      }

      @include max768{
        flex: 0 0 100%;
        max-width: 100%;
      }

      + .col-3{
        @include max768{
          margin-top: 30px;
        }
      }
    }
  }

  &-widget{
    font-size: 14px;
    @include max576{
      font-size: 12px;
    }
    + .footer-widget{
      margin-top: 40px;
    }
    &__title{
      display: block;
      font-size: 15px;
      font-family: $roboto-medium; 
      margin: 0 0 20px;

      @include max576{
        font-size: 14px;
      }

      &_icon{
        position: relative;
        @extend %clearfix;
        .svg-sprite{
          @extend %size_16; 
          margin-right: 10px;
          position: relative;
          top: -2px;
        }
      }
      &_light{
        font-family: $roboto-thin;
      }
    }
    &__space{
      &_border{
        border-top: 1px solid $color-white--light;
        margin-top: 40px;
        padding-top: 40px;
      }

      &_small{
        margin-top: 20px !important;
      }
    }
    &__max_width{
      max-width: 475px;
      @include max1024{
        max-width: 100%;
      }
    }
    &__subscribe{
      width: 100%;
      form{
        max-width: 475px;
      }
    }
    &__nav{
      &_list{
        @extend %reset_list;
        @extend %d_flex;
        @extend %justify_content__start;
        @extend %align_items__center;

        li{
          position: relative;
          margin-right: 10px;
          padding-right: 10px;
          &:after{
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            height: 16px;
            width: 1px;
            background: $color-white--light;
            display: block;
          }
          &:last-child{
            margin-right: 0;
            padding-right: 0;
            &:after{
              display: none;
            }
          }
          a{
            display: block;
            color: white;

            &:hover,
            &:focus{
              color: $color-white--light;
            }
          }
        }
      }
    }
    &__information{
      font-size: 13px;
      @include max576{
        font-size: 12px;
      }
      a{
        color: white;
        display: inline-block;
        border-bottom: 1px solid $color-white--light;
      
        &:hover,
        &:focus{
          color: $color-white--light;
        }
      }
    }
    &__socmed{
      .footer-widget__title{
        display: inline-block;
        margin-right: 5px;
      }
      ul{
        display: inline-block;
        text-align: left;
        list-style: none outside none;
        margin: 0;
        padding: 0;
        position: relative;
        top: -2px;
        li{
          margin-right: 5px;
          margin-bottom: 5px;
          display: inline-block;
        }
        a{
          display: block;

          &:hover,
          &:focus{
            opacity: .65;
          }
        }
        .svg-sprite{
          height: 16px;
          width: 16px;
        }
      }
    }
  }

